import { IWebinarCard } from '../../interfaces';

export const randomDateGenerator = () => {
  const date = new Date();
  date.setDate(date.getDate() + Math.floor(Math.random() * 30));
  const year = date.getFullYear();
  const month = (date.getMonth() + 1).toLocaleString('en-US', {
    minimumIntegerDigits: 2,
  });
  const day = date
    .getDate()
    .toLocaleString('en-US', { minimumIntegerDigits: 2 });
  return `${year}-${month}-${day}`;
};

const dummyIvfAcademy: IWebinarCard = {
  id: Math.random() * Math.random(),
  date: randomDateGenerator(),
  time: '10:00',
  link: 'https://ivf-academy.ru/kak-prodavat-na-platfom-ivf-i-pribyli',
  title: 'Как продавать на платформе ИВФ и получать прибыль',
  location: 'Zoom',
  speaker: {
    name: 'Александр Карасев',
    photo: 'https://ivf-academy.ru/wp-content/uploads/2020/05/alex-kras.jpg',
    profession: 'Президент ИВФ',
    company: 'ИВФ',
  },
};

export default dummyIvfAcademy;
